<template>
<div class="projectslider">
<p id="lp">Latest projects</p>
<div class="sss">
<div id="sliders">
<div class="slider slider-active" @mouseenter="showinfo" @mouseleave="hideinfo">
<div class="slider_elements" style="position: relative;">
<div style="position: absolute; bottom: 10%;">
<p class="sl_m">Shinai Public Service</p>
<p class="sl_d">My site for watching anime. I wrote it in my free time using the Vue framework.</p>
<div style="display: flex;" class="sl_t">
    <p class="t tl">JavaScript</p>
    <p class="t">Vue.js</p>
    <p class="t">Babel.js</p>
</div>
</div>

</div>
</div>

</div>
<div id="go_next" class="go_next">
</div>
</div>
</div>
</template>

<script>
export default {
    name: 'projectslider',
    data() {
        return {
            num: 0
        }
    },
    methods: {
        showinfo(e) {
            e.target.firstChild.style.opacity = '1'
            e.target.firstChild.classList.add('slider_elements-active')
        },
        hideinfo(e) {
            e.target.firstChild.style.opacity = '0'
            e.target.firstChild.style.animation = 'show_out_a 0.3s' 
            // e.target.firstChild.classList.remove('slider_elements-active')
        }
    },
    mounted() {
        const button = document.getElementById('go_next')
        const button2 = document.getElementsByClassName('slider')[0]
        const sl = document.getElementsByClassName('slider')
        button.addEventListener('click', () => {
            document.getElementById('go_next').classList.add('animated')
            setTimeout(() => {
                document.getElementById('go_next').classList.remove('animated')
            }, 300)
            if(this.num >= sl.length-1) { this.num = 0 }
            else { this.num += 1 }
            for(let i = 0; i < sl.length; i++) {
                if(this.num != i) {
                    sl[i].classList.remove('slider-active')
                } else {
                    sl[i].classList.add('slider-active')
                }

        }
        })
    }
}
</script>

<style scoped>
.sss {
    display: flex
}
.projectslider {
    margin-bottom: 100px;
    animation: show_in 0.95s;
    display: none;


}
.slider {
    display: none;
    width: 600px;
    height: 300px;
    background-color: red;
    background: url(@/assets/shinai_preview.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border: 3px solid rgb(43, 57, 67);
    border-radius: 14px;
    animation: show_awesome_2 .55s;
    transition: 0.6s;
}

.slider-active {
    display: inline-block;
    animation: show_awesome .55s;
}
@keyframes show_awesome_2 {
    from {
        opacity: 0;
        transform: translateY(0px) scale(1) translateX(130px);
    }
    to {
        opacity: 1;
        transform: translateX(0px) translateY(0px) scale(1);
    }
}
@keyframes show_awesome {
    from {
        opacity: 0;
        transform: translateY(0px) scale(1) translateX(-130px);
    }
    to {
        opacity: 1;
        transform: translateX(0px) translateY(0px) scale(1);
    }
}

#lp {
    color: var(--large-txt-color);
    font-family: 'Roboto';
    font-size: 28px;
    margin: 10px 0px 30px 0px;
    font-weight: 600;       
}
#go_next {
    width: 32px;
    height: 32px;
    background: url(@/assets/arrow.png);
    background-position: center;
    background-size: cover;
    filter: invert(100%);
    background-repeat: no-repeat;
    background-origin: content-box;
    padding: 14px;
    border-radius: 150px;
    border: 2.5px solid black;
    margin: auto auto auto 50px;
    transform: scale(1);
    transition: .1s;
    
}
.animated {
    animation: gonext 0.3s;
}
@keyframes gonext {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}
.slider_elements {
    background: rgba(7, 9, 13, 0.75);
    backdrop-filter: brightness(0.5);
    display: none;
    border-radius: 11.5px;
    height: 100%;
    opacity: 0;
    transition: 0.3s;
}
.slider_elements-active {
    display: block;
    opacity: 1;
    animation: show_in_a 0.3s;
}
.sl_m {
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 600;
    margin: 0;
    padding-top: 15px;
    z-index: 9;
    color: var(--large-txt-color);
    padding-left: 30px;
    padding-right: 30px;
    animation: show_in 0.3s;
}
.sl_d {
    font-family: 'Roboto';
