    padding-left: 30px;
    padding-right: 30px;
    text-align: justify;
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    animation: show_in 0.4s;
}
.sl_t {
    font-family: 'Roboto Mono';
    font-size: 14px;
