<template>
    <div class="settingsm" v-bind:class="{ 'settingsm-active': settings }"></div>
<div class="headers hidden" id="headers">

<img src="@/assets/rodukov.png" alt="custom_logo" class="unselectable logo">
<div class="items">
    <p class="item" v-on:click="item('about_me')"><span class="gg">01.</span> About</p>
    <p class="item" v-on:click="item('worked_me')"><span class="gg">02.</span> Experience</p>
    <p class="item" v-on:click="item('projectslider')"><span class="gg">03.</span> Portfolio</p>
    <p class="item" v-on:click="item('contact')"><span class="gg">04.</span> Contact</p>
    <!--img src="@/assets/settings2.svg" style="width: 23px; height: 24px; margin: auto; margin-left: 20px; filter: invert(89%) sepia(77%) saturate(7142%) hue-rotate(178deg) brightness(101%) contrast(94%);" v-on:click="settings = !settings" class="settings"-->
</div>

</div>
</template>

<script>
export default {
    name: 'headers',
    data() {
        return {
            settings: false
        }
    },
    mounted() {
        window.onscroll = function(e) {
        let headers = document.getElementsByClassName('headers')[0]

        if(!(this.oldScroll > this.scrollY)) {
            headers.classList.add('hidden')
        } else {
            headers.classList.remove('hidden')
        }

        this.oldScroll = this.scrollY;
        }  
    },
    methods: {
        item(text) {
            let y = document.getElementsByClassName(text)[0].getBoundingClientRect().top + window.scrollY;
            window.scroll({
            top: y,
            behavior: 'smooth'
            });
        }
    }
}
</script>

<style scoped>

.items {
    display: flex; margin: auto 0 auto auto; padding-right: 30px;
}

.headers {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 50px;
    background-color: #0a193062;
    backdrop-filter: blur(10px);
    z-index: 997;
    display: flex;
    padding-top: 10px;
    padding-bottom: 10px;
    box-shadow: 0px 10px 10px rgba(30, 30, 30, 0.212);
    transition: 0.25s;
    animation: show_in_a 0.25s;
}
.item {
    color: var(--large-txt-color);
    font-family: 'Roboto Mono';
    letter-spacing: 0px;
    font-size: 14px;
    text-decoration: none;
    margin-left: 5px;
    margin-right: 5px;
    
}
.item:before {
    content: "";
    width: 10px;
    height: 10px;
    background: url(@/assets/arrow3.png);
    border-radius: 10px;
    top: 10px;
    right: 5px;
    margin-right: 10px;
    display: inline-block;
}
.settings {
    transform: rotateZ(0deg);
    transition: 0.7s;
}
.settings:hover {
    transform: rotateZ(180deg);
}
.settings:active {
    transform: rotateZ(180deg) scale(1.1);
}
.gg {
    color: var(--maincolor)
}
.hidden {
    transform: translateY(-70px);
}
.settingsm {
    background-color: red;
    width: 100px;
    height: 100px;
    position: fixed;
    left: 50%;
    top: 50%;
    z-index: 996;
    transform: translate(-50%,-50%);
    animation: show_out_a 0.1s;
    opacity: 0;
}
.settingsm-active {
    animation: show_in_a 0.1s;
    opacity: 1;
}

.logo {
    width: 260px; margin: auto 0 auto 30px;
}

@media (max-width: 900px) {
    .items {
        display: none;
    }
    .logo {
        margin: auto;
    }
}
</style>
