<template>
<div class="Home">

<headers/>
<init/>
<whoami/>
<description/>
<involved/>
<projectslider/>
<repos/>
<contact/>
<footers/>

</div>
</template>

<script>
import init from '@/components/init.vue'
import whoami from '@/components/whoami.vue'
import description from '@/components/description.vue'
import involved from '@/components/involved.vue'
import projectslider from '@/components/projectslider.vue'
import headers from '@/components/headers.vue'
import repos from '@/components/repos.vue'
import contact from '@/components/contact.vue'
import footers from '@/components/footers.vue'


export default {
    name: 'HomeView',
    components: { init, whoami, description, involved, projectslider, headers, repos, contact, footers },
    mounted() {
        setTimeout(() => {
            document.getElementById('headers').classList.remove("hidden");
            this.add_showin(document.getElementsByClassName("about_me")[0])
            this.add_showin(document.getElementsByClassName("worked_me")[0])
            this.add_showin(document.getElementsByClassName("projectslider")[0])
            this.add_showin(document.getElementsByClassName("repos")[0])
            this.add_showin(document.getElementsByClassName("contact")[0])
            this.add_showin(document.getElementsByClassName("footers")[0])
            // document.getElementsByClassName('about_me')[0].style.display = "none";
        }, 12500)  /* DEBUG: 12500 */        
        setTimeout(() => {
            document.title = "Gleb Rodukov | Personal Website"
            document.body.style.transition = "0.796s"
            document.body.style.background = '#0A1930'
            document.body.style.height = '1100px'
        }, 12500)
    },
    methods: {
        add_showin(ele) {
            window.addEventListener("scroll", function(event) {
                let element = ele;
                let y = element.offsetTop;
                if (y*2 < window.scrollY) {
                    element.style.display = "block";
                }
            })
        }
    }
}
</script>

<style scoped>
.Home {
    margin-left: 16%;
    margin-right: 16%;
    margin-top: 145px;
}
@media (max-width: 1050px) {
    .Home {
        margin-left: 8%;
        margin-right: 8%;        
    }
}


</style>