<template>
<div id="init">
    
    <div class="absolute-init" style="top: 10%; left: 80%;"></div>
    <div class="absolute-init" style="top: 70%; left: 10%;"></div>
    <div class="absolute-init" style="top: 5%; left: 40%;"></div>
    <p class="main-init ">
        <span class="init-default unselectable">Hello, </span>
        <span class="init-default unselectable">World! </span>
        <span class="init-default unselectable">My </span>
        <span class="init-default unselectable">name </span>
        <span class="init-default unselectable">is </span>
        <span class="init-default unselectable">Gleb </span>
        <span class="init-default unselectable">Rodukov. </span>
        <span class="init-default unselectable">I'm </span>
        <span class="init-default unselectable">a </span>
        <span class="init-default unselectable">Front-end </span>
        <span class="init-default unselectable">Developer. </span>
        <span class="init-default unselectable">Let </span>
        <span class="init-default unselectable">me </span>
        <span class="init-default unselectable">show </span>
        <span class="init-default unselectable">a </span>
        <span class="init-default unselectable">little </span>
        <span class="init-default unselectable">bit </span>
        <span class="init-default unselectable">more </span>
        <span class="init-default unselectable">information </span>
        <span class="init-default unselectable">about </span>
        <span class="init-default unselectable">myself. </span>


        

    </p>
</div>
</template>

<script>
export default {
    name: 'init',
    mounted() {
        /* init document.title */
        document.title = "Gleb Rodukov | Personal Website"
        /* Get the words container */
        let main = document.getElementsByClassName('main-init')[0]
        
        /* Add chunk-to-text animation adder */
        for(let i = 0; i < main.childNodes.length; i++) {
            /* Remove opacity by this.change (by formula text*time) */
            setTimeout(() => {
                    this.change(main.childNodes[i], 1, 10)
                    /* Remove blur by this.change (by formula text*time+fixed_time)*/
                    setTimeout(() => {
                        document.title = main.childNodes[i].innerText
                        this.change(main.childNodes[i], 1, 0)
                    }, 300);
            }, i*500);

        }

        /* Remove init.vue (by formula nodes*time + pretime)*/
        setTimeout(() => {
            let init = document.getElementById('init')
            init.style.transition = '0.9s'; init.style.opacity = 0
            /* Fully removing init.vue */
            setTimeout(() => {
                init.remove()
            }, 901)
        }, main.childNodes.length*500+1000);
    },
    methods: {
        change(it, opacity, filter) { it.style.opacity = opacity; it.style.filter = `blur(${filter}px)`; },
        left: () => Math.floor(Math.random() * 100).toString()+'%' ,
        top: () => Math.floor(Math.random() * 100).toString()+'%'
    }
}
</script>

<style>
body {
    background: #020c1b; /* 04120a */

}
.init {
    transition: 1.4s;
}

.main-init {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-family: 'Roboto';
  font-weight: 500;
  color: #63FCD7;
  font-size: 35px;

}
.init-default {
    filter: blur(3px);
    opacity: 0;
    transition: 0.7s;
    transform: rotateX(10deg);
}
.absolute-init {
    position: absolute;
    background-color: #63fcf7;
    width: 70px;
    height: 70px;
    border-radius: 100%;
    filter: blur(70px);
    animation: to_move 4.1s infinite;
}
@keyframes to_move {
    0% {
        transform: translateY(50px);
    }
    50% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(50px);
    }
}
</style>