<script>
export default {
    name: 'involved',
    mounted() {

    },
    methods: {
        involve(e) {
            let d = document.getElementsByClassName('project')
            let c = document.getElementsByClassName('aboutproject')
            for(let i = 0; i<d.length; i++){
                d[i].classList.remove('project-active')
            }
            const btn = e.target
            btn.classList.add('project-active')

            for(let i = 0; i<document.getElementsByClassName('aboutproject').length; i++){
                document.getElementsByClassName('aboutproject')[i].classList.remove('aboutproject-active')
            }
            document.getElementsByClassName('aboutproject')[parseInt(btn.id.replace("p", ""))].classList.add('aboutproject-active')

        }
    }
}
